<script>
import { useAppOptionStore } from '@/apps/auth/stores/app-option';
import { RouterLink } from 'vue-router';
import axios from 'axios';
import { Toast, Modal } from 'bootstrap';
import Cookies from 'js-cookie';

export default {
	data() {
		return {
			year: (new Date()).getFullYear(),
			form: {
				username: '',
				password: '',
				token: {
					a: '',
					b: '',
					c: '',
					d: '',
					e: '',
					f: ''
				}
			},
			errorMessage: '',
			modal2FA: '',
			rememberMe: false // Status der "Angemeldet bleiben" Checkbox
		}
	},
	mounted() {
		const appOption = useAppOptionStore();
		appOption.appSidebarHide = true;
		appOption.appHeaderHide = true;
		appOption.appFooter = false;
		appOption.appContentClass = 'p-0';

		this.modal2FA = new Modal(document.getElementById('modal2FA'));

		const inputs = document.querySelectorAll(".otp-field > input");
		const button = this.$refs.submit2FAButton;

		window.addEventListener("load", () => inputs[0].focus());
		button.setAttribute("disabled", "disabled");

		document.getElementById('modal2FA').addEventListener('hide.bs.modal', () => {
			// Leere alle Token-Eingaben
			this.form.token = { a: '', b: '', c: '', d: '', e: '', f: '' };
			// Leere alle Input-Felder
			inputs.forEach(input => {
				input.value = '';
				input.setAttribute("disabled", "disabled"); // Disable all except the first one
			});
			inputs[0].removeAttribute("disabled");
			inputs[0].focus(); // Fokussiere auf das erste Feld
			button.classList.remove("active");
			button.setAttribute("disabled", "disabled");
		});

		// Validate inputs
		const validateInputs = () => {
			const allFilled = Array.from(inputs).every(input => input.value !== "");
			if (allFilled) {
				button.classList.add("active");
				button.removeAttribute("disabled");
			} else {
				button.classList.remove("active");
				button.setAttribute("disabled", "disabled");
			}
		};

		// Handle input, navigation, and paste events
		inputs.forEach((input, index) => {
			// Handle input event
			input.addEventListener("input", () => {
				let value = input.value.replace(/\D/g, ''); // Erlaube nur Zahlen
				if (value.length > 1) {
					value = value.slice(0, 1); // Beschränke auf eine Zahl pro Feld
				}
				input.value = value;
				if (input.value && index < inputs.length - 1) {
					inputs[index + 1].removeAttribute("disabled");
					inputs[index + 1].focus();
				}

				// Aktualisiere die Token-Variable basierend auf der Eingabe
				this.form.token[Object.keys(this.form.token)[index]] = input.value;
				validateInputs();
			});

			// Navigation with Arrow keys
			input.addEventListener("keydown", (e) => {
				if (e.key === "ArrowLeft" && index > 0) {
					// Go to the previous input
					inputs[index - 1].focus();
					setTimeout(() => inputs[index - 1].setSelectionRange(1, 1), 0);
				} else if (e.key === "ArrowRight" && index < inputs.length - 1) {
					// Go to the next input
					inputs[index + 1].focus();
					setTimeout(() => inputs[index + 1].setSelectionRange(1, 1), 0);
				} else if ((e.key === "Backspace" || e.key === "Delete") && input.value === "") {
					if (index > 0) {
						// Set the previous input to be empty
						inputs[index - 1].focus();
						inputs[index - 1].value = "";

						// Aktualisiere die Token-Variable beim Leeren des Eingabefelds
						this.form.token[Object.keys(this.form.token)[index - 1]] = "";
						setTimeout(() => inputs[index - 1].setSelectionRange(1, 1), 0);
					}

					// Leere das aktuelle Eingabefeld und die entsprechende Token-Variable
					input.value = "";
					this.form.token[Object.keys(this.form.token)[index]] = "";
				}
			});

			// Handle paste event (unchanged)
			input.addEventListener("paste", (e) => {
				e.preventDefault();
				const pasteData = e.clipboardData.getData("text").trim();
				const digits = pasteData.split('').filter(char => /\d/.test(char));

				digits.forEach((digit, i) => {
					if (i < inputs.length) {
						inputs[i].value = digit;
						this.form.token[Object.keys(this.form.token)[i]] = digit;
						inputs[i].removeAttribute("disabled");
					}
				});

				validateInputs();
				inputs[Math.min(digits.length, inputs.length - 1)].focus();
			});
		});


		// Disable all inputs except the first one
		inputs.forEach((input, index) => {
			if (index > 0) {
				input.setAttribute("disabled", "disabled");
			}
		});
	},
	beforeUnmount() {
		const appOption = useAppOptionStore();
		appOption.appSidebarHide = false;
		appOption.appHeaderHide = false;
		appOption.appFooter = true;
		appOption.appContentClass = '';
	},
	methods: {
		validateInputs(inputs, button) {
			// Enable the submit button if all inputs are filled
			const allFilled = Array.from(inputs).every(input => input.value !== "");
			if (allFilled) {
				button.classList.add("active");
				button.removeAttribute("disabled");
			} else {
				button.classList.remove("active");
				button.setAttribute("disabled", "disabled");
			}
		},
		async submitForm() {
			try {
				const response = await axios.post('https://saci.key2host.com/auth', {
					username: this.form.username,
					password: this.form.password
				});

				const cookieOptions = {
					domain: '.key2host.com',
					path: '/',
					sameSite: 'None',
					secure: true,
					expires: this.rememberMe ? 1 : undefined // Setzt das Cookie für 1 Tag, wenn "Angemeldet bleiben" markiert ist
				};
				Cookies.set("id_token", response.data.id_token, cookieOptions);
				Cookies.set("access_token", response.data.access_token, cookieOptions);

				window.location.href = 'https://cp.key2host.com/';
			} catch (error) {
				if (error.response && error.response.data == "2FA token required") {
					this.modal2FA.toggle();
				} else {
					this.errorMessage = error.response ? error.response.data : 'Die Anfrage konnte nicht verarbeitet werden. Versuche es später erneut!';
					new Toast(document.getElementById('toastError')).show();
				}
			}
		},
		async submit2FA() {
			const token = `${this.form.token.a}${this.form.token.b}${this.form.token.c}${this.form.token.d}${this.form.token.e}${this.form.token.f}`;
			try {
				const response = await axios.post('https://saci.key2host.com/auth', {
					username: this.form.username,
					password: this.form.password,
					token: token,
				});

				const cookieOptions = {
					domain: '.key2host.com',
					path: '/',
					sameSite: 'None',
					secure: true,
					expires: this.rememberMe ? 1 : undefined // Setzt das Cookie für 1 Tag, wenn "Angemeldet bleiben" markiert ist
				};
				Cookies.set("id_token", response.data.id_token, cookieOptions);
				Cookies.set("access_token", response.data.access_token, cookieOptions);

				window.location.href = 'https://cp.key2host.com/';
			} catch (error) {
				this.modal2FA.toggle();
				this.errorMessage = error.response ? error.response.data : 'Dein Code konnte nicht geprüft werden. Versuche es später erneut!';
				new Toast(document.getElementById('toastError')).show();
			}
		},
		togglePWV() {
			const eye = document.getElementById("pweye");
			const pwf = document.getElementById("pwField");
			if (eye.classList.contains("fa-eye")) {
				eye.classList.replace("fa-eye", "fa-eye-slash");
				pwf.type = "text";
			} else {
				eye.classList.replace("fa-eye-slash", "fa-eye");
				pwf.type = "password";
			}
		}
	}
}
</script>
<style>
.otp-field {
	flex-direction: row;
	column-gap: 10px;
	display: flex;
	align-items: center;
	justify-content: center;
}

.otp-field input {
	height: 45px;
	width: 42px;
	border-radius: 6px;
	outline: none;
	font-size: 1.125rem;
	text-align: center;
	border: 1px solid #ddd;
}

.otp-field input:focus {
	box-shadow: 0 1px 0 rgba(0, 0, 0, 0.1);
}

.otp-field input::-webkit-inner-spin-button,
.otp-field input::-webkit-outer-spin-button {
	display: none;
}
</style>
<template>
	<!-- BEGIN login -->
	<div class="login">
		<!-- BEGIN login-content -->
		<div class="login-content">
			<form v-on:submit.prevent="submitForm">
				<h1 class="text-center">{{ $t("AUTH.LOGIN.TITLE") }}</h1>
				<div class="text-inverse text-opacity-50 text-center mb-4">
					{{ $t('AUTH.LOGIN.DESC') }}
				</div>
				<div class="mb-3">
					<div class="d-flex">
						<label class="form-label">{{ $t('GENERAL.EMAIL') }} <span class="text-danger">*</span></label>
					</div>
					<div class="input-group flex-nowrap">
						<i class="fa-solid fa-image-portrait input-group-text" id="addon-wrapping"></i>
						<input v-model="form.username" type="email"
							class="form-control form-control-lg bg-white bg-opacity-5 fs-14px" autocomplete="email"
							required />
					</div>
				</div>
				<div class="mb-3">
					<div class="d-flex">
						<label class="form-label">{{ $t('GENERAL.PASSWORD') }} <span
								class="text-danger">*</span></label>
						<RouterLink to="/recover" class="ms-auto text-inverse text-decoration-none text-opacity-50">{{
							$t('AUTH.LOGIN.PWFORGOT') }}</RouterLink>
					</div>
					<div class="input-group flex-nowrap">
						<i class="fa-solid fa-key input-group-text" id="addon-wrapping"></i>
						<input v-model="form.password" type="password" id="pwField"
							class="form-control form-control-lg bg-white bg-opacity-5 fs-14px" autocomplete="password"
							required />
						<button type="button" @click="togglePWV" class="input-group-text"><i id="pweye"
								class="fa fa-eye"></i></button>
					</div>
				</div>
				<div class="mb-3">
					<div class="row align-items-center">
						<div class="col-6">
							<div class="form-check">
								<input class="form-check-input" type="checkbox" id="rememberCheck"
									v-model="rememberMe" />
								<label class="form-check-label" for="rememberCheck">{{ $t('AUTH.LOGIN.REMEMBER')
									}}</label>
							</div>
						</div>
						<div class="col-6 text-end">
							<RouterLink to="" class="text-body">
								<i class="fa-brands fa-apple fa-xl m-2 align-middle" style="height: 0.2em;"></i>
							</RouterLink>
							<RouterLink to="" class="text-body">
								<i class="fa-brands fa-google fa-lg m-2 align-middle"></i>
							</RouterLink>
							<RouterLink to="" class="text-body">
								<i class="fa-brands fa-microsoft fa-lg m-2 align-middle"></i>
							</RouterLink>
						</div>
					</div>
				</div>
				<button type="submit" class="btn btn-outline-theme btn-lg d-block w-100 fw-500 mb-3">{{
					$t('AUTH.LOGIN.TITLE') }}</button>
			</form>
			<div class="fixed-bottom mb-2 text-center small text-inverse text-opacity-50">
				Copyright &copy; {{ year }} <a href="https://www.key2host.com/" target="_blank">Key2Host</a> - {{
					$t("APP.LIC") }}
			</div>
		</div>
		<!-- END login-content -->
	</div>
	<!-- END login -->

	<!-- BEGIN #modal2FA -->
	<div class="modal fade" id="modal2FA" data-bs-backdrop="static" data-bs-keyboard="false" tabindex="-1"
		aria-labelledby="modal2FALabel" aria-hidden="true">
		<div class="modal-dialog modal-dialog-centered">
			<div class="modal-content">
				<div class="modal-header">
					<h5 class="modal-title" id="modal2FALabel">2-Faktor Authentifizierung</h5>
				</div>
				<div class="modal-body">
					<div class="row d-flex justify-content-center">
						<div class="col-12 text-center">Da Sie die Zwei-Faktor-Authentifizierung in Ihrem Konto
							eingerichtet haben,
							müssen Sie nun den Code aus Ihrer Authenticator-App (TOTP) eingeben, um mit der Anmeldung
							fortzufahren.</div>
						<form id="2faForm" class="otp-field col-3 mt-3" v-on:submit.prevent="submit2FA">
							<input v-model="form.token.a" type="text" inputmode="numeric" class="form-control"
								autocomplete="one-time-code" autofocus required />
							<input v-model="form.token.b" type="text" inputmode="numeric" class="form-control" autofocus
								required disabled />
							<input v-model="form.token.c" type="text" inputmode="numeric" class="form-control" autofocus
								required disabled />
							<input v-model="form.token.d" type="text" inputmode="numeric" class="form-control" autofocus
								required disabled />
							<input v-model="form.token.e" type="text" inputmode="numeric" class="form-control" autofocus
								required disabled />
							<input v-model="form.token.f" type="text" inputmode="numeric" class="form-control" autofocus
								required disabled />
						</form>
					</div>
				</div>
				<div class="modal-footer">
					<button type="button" class="btn btn-outline-default" data-bs-dismiss="modal">Abbrechen</button>
					<button type="submit" class="btn btn-primary" form="2faForm" ref="submit2FAButton">Anmelden</button>
				</div>
			</div>
		</div>
	</div>
	<!-- END #modal2FA -->

	<!-- BEGIN #toastError -->
	<div class="toasts-container">
		<div class="toast bg-warning bg-opacity-50" data-autohide="true" id="toastError">
			<div class="toast-header">
				<i class="far fa-triangle-exclamation text-muted me-2"></i>
				<strong class="me-auto">Fehler</strong>
				<small>Jetzt</small>
			</div>
			<div class="toast-body">
				{{ this.errorMessage }}
			</div>
		</div>
	</div>
	<!-- END #toastError-->
</template>
